/**
 * 
 * 
 * @param {*} func 
 * @param {number} [delay=150] 
 * @return 
 */
export function debounce(func, delay = 150) {
  let timer = 0;
  return function() {
    clearTimeout(timer);
    timer = setTimeout(() => {
      console.log('timer...', timer)
      func();
    }, delay)
  }
}

/**
 * 
 * 
 * @param {*} func 
 * @param {number} [delay=150] 
 * @return 
 */
export function throttle(func, delay = 150) {
  let timer = +new Date();
  return function() {
    let cur = +new Date();
    if(cur - timer > delay) {
      timer = cur;
      setTimeout(() => {
        func();
      }, delay)
    }
  }
}


